* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.newhome {
  display: flex;
  justify-content: center;
  background-color: #f8f8f8;
  margin: 5% 20% 5% 20%;
  border: solid 2px rgba(183, 187, 183, 0.164);
  border-radius: 10px;
  flex-direction: row;
}
.newhome-left {
  width: 50%;
  padding-left: 45px;
  padding-top: 45px;
}
.newhome-left-title {
  font-size: 50px;
  padding-right: 45px;
  color: #00796b;
}
.newhome-left-text {
  padding-top: 15px;
  padding-right: 45px;
  font-size: 20px;
  color: rgb(102, 102, 102);
}
.newhome-left-items {
  display: flex;
  padding-top: 35px;
  gap: 25px;
}
.newhome-left-items-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.newhome-left-items-item-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(2);
  color: #00796b;
  border: solid 1px #00796b;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  margin-bottom: 20px;
  background-color: #dfdfdf;
}
.newhome-left-items-item-number {
  font-size: 24px;
  font-weight: bold;
}
.newhome-left-items-item-text {
  font-size: 12px;
}
.newhome-right {
  display: flex;
  width: 50%;
  align-items: center;
  justify-content: center;
}
.newhome-right img {
  width: 100%;
}

@media (max-width: 1024px) {
  .newhome {
    flex-direction: column;
    margin: 5% 15% 5% 15%;
  }
  .newhome-left {
    width: 100%;
  }
  .newhome-left-items {
    justify-content: center;
  }
  .newhome-right {
    width: 100%;
  }
}
@media (max-width: 768px) {
  .newhome {
    margin: 5% 10% 5% 10%;
  }
  .newhome-left {
    padding-left: 15px;
  }
  .newhome-left-title {
    font-size: 40px;
    padding-right: 15px;
  }
  .newhome-left-text {
    padding-right: 15px;
  }
}
.item_detail {
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #fff;
  border-radius: 1rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  color: #333;
}
.item_detail img {
  width: 100%;
