}

.prop_price {
  font-weight: 200;
  margin-bottom: 12px;
}
.prop_price span {
  font-weight: 700;
  color: #009688;
  font-size: 24px;
}

.prop_city {
  font-size: 20px;
  font-weight: 600;
  margin: 0 0 6px 0;
  color: #1e293b;
}

.prop_address {
  font-size: 16px;
  color: #64748b;
  margin: 0 0 12px 0;
}

.prop_details {
  display: flex;
  flex-wrap: nowrap;
  gap: 16px;
  font-size: 16px;
  color: #64748b;
  align-items: center;
}
.prop_details p {
  display: flex;
  align-items: center;
  margin: 0;
  gap: 6px;
  white-space: nowrap;
}

.bi-aspect-ratio {
  color: #009688 !important;
}

.prop_icon {
  width: 18px !important;
  height: 18px;
  display: inline-block;
  color: #009688;
