  font-weight: 500;
  color: #374151;
  margin-bottom: 6px;
  letter-spacing: -0.01em;
}

.form-auth-input {
  width: 100%;
  padding: 14px 16px;
  font-size: 16px;
  font-weight: 400;
  color: #1f2937;
  background: #ffffff;
  border: 1.5px solid #e5e7eb;
