  font-size: 16px;
  font-weight: 600;
  color: #ffffff;
  background: linear-gradient(135deg, #00796b 0%, #004d40 100%);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 12px;
  letter-spacing: -0.01em;
  position: relative;
  overflow: hidden;
}
.form-auth-button:hover {
  color: black;
  background: #e0def7;
  box-shadow: 0 4px 12px rgba(224, 222, 247, 0.6);
  transform: translateY(-2px);
}
.form-auth-button:active {
  transform: translateY(0);
  box-shadow: 0 4px 15px rgba(37, 99, 235, 0.2);
}
.form-auth-button.loading {
  pointer-events: none;
}
.form-auth-button.loading::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin: -10px 0 0 -10px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top-color: #ffffff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

